.blog-list {
	.post.sticky {
		display: block;
		border-color: $color__theme;

		.blog-content {

			.entry-title {
				a {
					color: $color__theme;
				}
			}
		}
	}
}

.updated:not(.published) {
	display: none;
}

.no-results {
	.page-content {
		padding: 50px 0 120px;
		text-align: left;

		@media(max-width: 991px) {
			padding: 50px 0 80px;
		}
	}
}

.page-content,
.entry-content,
.entry-summary {
	word-break: break-word;

	&:after {
		content: '';
		clear: both;
		display: block;
	}
}

.page-header {
	.page-title {
		font-size: 40px;
		margin-top: 0;
		margin-bottom: 50px;
	}
}

.page-content {
	padding: 120px 0;
}

.page-template-default {
	.page-content {
		padding: 114px 0 97px;

		.comments-area {
			padding: 34px 0 23px;
		}

		.page-links {
			margin-bottom: 20px;
		}
	}
}

.blog-post-archive {
	padding: 120px 0;
}

.search-no-results {
	.page-content {
		.page-content {
			padding: 20px 0 0;
		}
	}
}

.post-author {
	display: inline-block;
	font-size: 13px;
	color: #92929f;
	font-weight: 500;

	img {
		border-radius: 50% !important;
		margin-right: 10px;
	}

	&:hover {
		color: $color__theme;
	}
}


.post-wrapper,
.blog-posts {
	.blog-post-list {
		margin-bottom: 40px;
	}
}

.blog-archive-wrapper {
	.sidebar_right {
		#main-content {
			@media(min-width: 1200px) {
				padding-right: 30px;
			}
		}
	}
}


/* POst Grid */

.post-grid {
	position: relative;
	background: #fff;
	transition: all 0.3s ease-in-out;
	margin-bottom: 30px;
	border-radius: 15px;
	overflow: hidden;
	box-shadow: 0 10px 20px rgba(0, 0, 0, 0.05);

	.meta-category-wrapper {

		a {
			display: inline-block !important;
			border: 0;
			font-size: 13px;
			font-weight: 600;
			line-height: 1;
			padding: 6px 15px;
			background-color: rgba(23, 92, 255, 0.15);

			&:hover {
				color: #fff;
				background: $color__theme;
			}


			&:not(:last-child) {
				margin-right: 7px;
			}
		}
	}

	.feature-image {
		position: relative;
		overflow: visible;

		&:hover {
			img {
				transform: scale(1);
			}
		}
	}

	.blog-content {
		padding: 19px 30px 40px;

		.entry-title {
			font-size: 20px;
			line-height: 1.2;
			font-weight: 600;
			margin: 14px 0 9px;
		}

		.author {
			span {
				display: inline-block;
				margin-right: 5px;
			}
		}

		.author-simple {
			a {
				color: #5c5c60;

				&:hover {
					color: $color__theme;
				}
			}
		}

		p {
			margin-bottom: 26px;
		}

		.read_more_btn {
			display: inline-flex;
			align-items: center;
			text-transform: uppercase;
			font-weight: 600;
			font-size: 14px;


			i {
				margin-left: 5px;
				color: $color__heading;
			}

			&:hover {
				i {
					margin-left: 8px;
					transition: all 0.3s ease-in-out;
					color: $color__theme;

				}
			}
		}

	}

	&:hover {
		background-color: rgb(255, 255, 255);
		box-shadow: 0px 30px 50px 0px rgba(1, 4, 45, 0.08);

		.feature-image {
			img {
				transform: scale(1);
			}
		}
	}
}

.post-meta {
	margin-bottom: 32px;
	padding: 5px 0 0;
	line-height: 1;

	li {
		display: inline-block;
		position: relative;
		font-size: 14px;
		color: #696969;

		i {
			margin-right: 8px;
			font-size: 16px;
			color: $color__theme;
			line-height: 0.9;
		}

		&:not(:last-child) {
			margin-right: 20px;

			@media(max-width: 480px) {
				margin-bottom: 15px;
			}
		}

		.author {
			a {
				color: #5c5c60;
				font-weight: 400;
				font-size: 15px;

				&:hover {
					color: $color__theme;
				}
			}
		}

		a {
			margin: 0;
			color: #8D8D95;
			line-height: normal;
			font-size: 14px;
			font-weight: 500;

			&:hover {
				color: $color__theme;
			}
		}
	}
}

.post-categories {
	margin: 0;
	padding: 0;
	list-style: none;

	li {
		display: inline-block;
		position: relative;

		&:not(:last-child) {
			a {
				&:after {
					content: '/';
					position: absolute;
					right: -15px;
					top: 0;
				}
			}
		}
	}
}

.entry-meta,
.post-categories {
	a {
		color: $color__theme;
		font-size: 12px;
		display: inline-block;
		text-transform: uppercase;
		margin-right: 20px;
		position: relative;
		line-height: 1.2;
		font-weight: 400;

		&:not(:last-child) {
			&:after {
				content: '/';
				position: absolute;
				right: -15px;
				top: 0;
			}
		}
	}
}


/** Blog List */
.blog-post-list {
	overflow: hidden;
	background: #fff;
	border-radius: 6px;

	&.tag-title {
		&:after {
			display: none;
		}
	}

	&.sticky {
		box-shadow: none;

		.entry-title {
			a {
				color: $color__theme;
			}
		}
	}

	.post-thumbnail-wrapper {
		position: relative;
	}

	.gpt-blog-content {
		padding: 30px;
	}

	ul.entry-meta {
		li {
			color: #818181;

			a {
				color: #818181;
			}

		}
	}

	.post-thumbnail {
		position: relative;
		display: block;
		overflow: hidden;


		img {
			border-radius: 8px;
			transform: scale(1.03);
			transition: all 0.3s ease-in-out;
		}

		&:hover {
			img {
				transform: scale(1.05);
			}
		}
	}

	.entry-header {
		padding: 50px 50px 0;

		.entry-title {
			font-size: 24px;
			font-weight: 700;
			-ms-word-wrap: break-word;
			word-wrap: break-word;
		}
	}


	.entry-title {
		font-size: 30px;
		line-height: 1.2;
		font-weight: 700;
		margin-bottom: 0;
		word-wrap: break-word;
		word-break: break-all;

		a {
			color: $color__heading;
			position: relative;
			background-image: linear-gradient(currentColor, currentColor);
			background-size: 0 2px;
			background-position: 0 100%;
			background-repeat: no-repeat;
			transition: background-size 0.4s cubic-bezier(0, .57, .55, 1);
			will-change: background-size;

			&:hover {
				background-size: 100% 2px;
				color: unset;
			}

		}
	}

	.entry-content {
		p {
			margin-bottom: 0;
			color: #B7B7C5;
		}
	}

	.entry-meta-wrapper {
		margin: 20px 0;
	}

	.read-more {
		margin-top: 25px;

		.gpt-btn.btn-sm {
			padding: 12px 25px;
			font-size: 15px;
			border-color: #e5e5e5;
			color: #5c5c60;
			border-radius: 4px;

			&:hover {
				color: #fff;
				border-color: $color__theme;
			}
		}
	}


	&.sticky {
		.post-thumbnail {
			margin-bottom: 0;
		}

		.entry-header {
			.entry-title {
				a {
					color: $color__theme;

					&:hover {
						color: $color__theme;
					}
				}
			}
		}
	}

}

ul.entry-meta {
	margin: 0;
	padding: 0;
	list-style: none;
	display: flex;
	gap: 25px;

	li {
		font-family: $font__heading;
		text-transform: uppercase;
		color: #666;
		font-size: 14px;
		font-weight: 700;
		display: inline-flex;
		align-items: center;
		gap: 4px;

		a {
			font-size: 14px;
			font-weight: 700;
			color: #666;
			display: inline-block;
			position: relative;
			line-height: 1;
			transition: all 0.3s ease-in-out;
			margin-right: 0;

			&:hover {
				color: $color__theme;
			}
		}
	}
}

