h3.post-share-title {
	font-size: 18px;
	margin-bottom: 10px;
}

.social-share-link {
	margin: 0;
	padding: 0;
	list-style: none;


	li {
		display: inline-block;

		&:not(:last-child) {
			margin-bottom: 7px;
		}

		a {
			display: block;

			i {
				font-size: 15px;
				position: relative;
				height: 40px;
				width: 44px;
				line-height: 40px;
				text-align: center;
				display: block;
				border-radius: 5px;
				color: #fff;
				background: $color__theme;
				transition: all 0.3s ease-in-out;

				&.fa-facebook-f {
					background: #1559c2;
				}

				&.fa-x-twitter {
					background: #1da1f2;
				}

				&.fa-pinterest-p {
					background: #bd081c;
				}

				&.fa-linkedin-in {
					background: #0077b5;
				}
			}


			&:hover {

				i {
					opacity: 0.8;
				}
			}

		}
	}
}

.gpt-btn-link {
	display: flex;
	align-items: center;
	font-size: 15px;
	font-weight: 600;
	color: $color__heading;

	i {
		font-size: 80%;
		margin-left: 7px;
	}
}

.tag_list {
	margin-top: 30px;

	.tags {
		color: #051441;
		text-transform: uppercase;
		letter-spacing: 2px;
		display: inline-block;
		margin-right: 5px;
		margin-bottom: 10px;
		font-weight: 600;
	}
}

