blockquote, .wp-block-quote {
	padding-left: 40px;
	border-left: 0;
	background: transparent;
	border-left: 4px solid #fff;
	overflow: hidden;
	position: relative;

	&:before {
		content: "\f10d";
		font-family: "Font Awesome 5 Free";
		font-weight: 900;
		position: absolute;
		top: 0;
		left: 10px;
		line-height: 1;
		color: rgba(255, 255, 255, 0.1);
		border-radius: 50%;
		font-size: 45px
	}

	p {
		font-family: $font__heading;
		font-size: 30px;
		line-height: 1.4;
		color: #fff;
		font-style: normal;
		margin-bottom: 4px;
		font-weight: 300;
		position: relative;


		cite {
			margin-left: 0;
		}
	}


	cite {
		font-family: $font__main;
		font-size: 18px;
		font-weight: 400;
		font-style: normal;
		color: $color__heading;
		padding-top: 23px;
		display: inline-block;
		position: relative;
	}
}

