// Single Post

.blog-single-content-area {
	padding: 80px 0;
}

.no-sidebar {
	.post-single {
		max-width: 1000px;
		margin: 0 auto;


	}

	&.single-post {
		#comments {
			max-width: 1000px;
			margin: 93px auto 0;
		}
	}

}


.post-single {
	margin-bottom: 0;

	.feature-image {
		margin-bottom: 45px;
	}

	.entry-header {
		padding-bottom: 25px;
	}

	.entry-title {
		font-size: 36px;
		margin: 10px 0 25px;
	}

	.wp-block-image {
		margin-top: 30px;
	}

	.blocks-gallery-grid {
		li {
			img {
				border-radius: 10px;
			}
		}
	}

	blockquote {
		p {
			margin: 0;
		}
	}

	.wp-block-image {
		text-align: center;
	}

	.wp-block-image {
		margin-bottom: 39px;
	}

	.edit-link {
		margin-left: 15px;
	}


	.single-post-title {
		font-size: 36px;
		font-weight: 600;
		margin-bottom: 10px;
		-ms-word-wrap: break-word;
		word-wrap: break-word;
		line-height: 1.3;
	}

	.entry-meta {
		margin-bottom: 30px;
	}


	.post-meta-cat {
		margin-bottom: 20px;

		a {
			height: 30px;
			line-height: 30px;
			padding: 0 30px;
			font-size: 18px;
			text-transform: capitalize;
			color: #fff;
			border-radius: 5px;
			background-color: #37a45d;
			min-width: auto;
			font-weight: 400;
			margin: 0 5px;
			border: 0px solid transparent;
			display: inline-block;

			&:first-child {
				background-color: #e9683e;
			}
		}
	}

	.meta-wrapper {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.feature-image {
		text-align: center;
		overflow: hidden;

		img {
			border-radius: 6px;
			transform: scale(1.01);
		}
	}

	.entry-footer {
		margin-top: 50px;
		width: 100%;
		display: flex;
		justify-content: space-between;
		border-top: 1px solid rgba(255, 255, 255, 0.1);
		border-bottom: 1px solid rgba(255, 255, 255, 0.1);
		align-items: center;
		padding: 20px 0;

		.tag_list {
			padding-bottom: 10px;
			border-bottom: 1px solid #e8ebf4;
		}
	}

	.tag-title {
		display: block;
		margin-right: 10px;
		font-size: 18px;
	}

	.single-tagcloud {
		a {
			display: inline-block;
			font-size: 14px;
			color: #646464;
			border: 1px solid #e5e5e5;
			border-radius: 4px;
			padding: 9px 15px;
			background-color: #fff0;
			transition: all ease 0.31s;
			text-transform: lowercase;
			margin-right: 6px;
			margin-bottom: 6px;
			line-height: 1.4;

			&:hover {
				background-color: $color__theme;
				border-color: $color__theme;
				color: #fff;
			}
		}
	}
}

