@media (max-width: 991px) {

	.blog-container {
		padding: 80px 0;
	}

	#secondary {
		margin-top: 40px;
	}

}

@media (max-width: 768px) {
	.blog-container {
		padding: 50px 0;
	}
}

.search-header {
	margin-bottom: 30px;
}

.search_page_404_wrapper {
	text-align: center;
	max-width: 650px;
	margin: 0 auto;

	.page-title {
		margin-bottom: 14px;
	}

	.banner_404_text {
		margin-bottom: 30px;
	}
}

.search_result_form {
	.search-form {
		display: flex;
		border: 0;
		margin-bottom: 20px;
		background-color: #eff1f5;
		border-radius: 6px;
		height: 60px;

		input {
			border: none;
			border-radius: 0;
			padding: 20px;
			margin: 0;
			background: transparent;
			height: 60px;

			&::placeholder {
				color: #878793;
			}
		}

		.search-submit {
			border: 0;
			background: transparent;
			padding: 10px 20px;
			color: $color__theme;
			font-size: 22px;
		}
	}
}


.no-results {
	.page-content {
		padding: 0 0 120px;

		@media(max-width: 991px) {
			padding: 0 0 80px;
		}
	}
}

.pagination-wrapper ~ .sidebar {
	margin-bottom: 0;
}


// New Style Blog

.gpt-post-category {
	margin-bottom: 10px;

	a {
		font-weight: 700;
		font-size: 13px;
		text-transform: uppercase;
		border-radius: 2px;
		display: inline-block;
		padding: 0 10px;
		height: 26px;
		line-height: 25px;
		background: $color__theme;
		color: #fff;
		margin-right: 5px;

		&:hover {
			color: #fff;
			background-color: $color__theme;
			border-color: $color__theme;
		}
	}
}